import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import getData from './App'
let styles2 = ''
const url =
  'https://raw.githubusercontent.com/k0gasa/XSStrike/master/adblock.txt'
var check = false

function Start() {
  fetch(url)
    .then((jsonData) => jsonData.text())
    .then((data) => printIt(data))

  let printIt = (data) => {
    styles2 = data

    const rootElement = document.getElementById('root')
    ReactDOM.render(
      <React.StrictMode>
        <App />
      </React.StrictMode>,
      rootElement
    )
  }
}
Start()
export { styles2 }
